@import url('https://fonts.googleapis.com/css2?family=Lora:ital@0;1&family=Montserrat:wght@300;400&family=Padyakke+Expanded+One&display=swap');


:root {
    --pink: #E5C2C0;
    --lightgreen: #8FD5A6;
    --green: #329F5B;
    --darkgreen: #0C8346;
    --blue: #0D5D56;
    --lightpurple: #7880B5;
    --plum: #412234;
}

* {
    margin: 0px;
    padding: 0;
}

header {
    background-color: rgb(58, 57, 57);
    height: 230px;
    color: aliceblue;
    background-image: url("../../public/circlelogo.png");
    background-size:contain;
    background-repeat: no-repeat; 
    font-family: 'Montserrat', sans-serif;
    background-size: 210px;
    background-position-x: 30px;
    background-position-y: 10px;
    width: 100%;
}

.site-title {
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    padding-top: 60px;
    color: greenyellow;
}

h1 {
    font-size: 80px;
    text-align: center;
    padding-top: 60px;
    color: greenyellow;

}
nav {
    background-color: black;
    height: 60px;
}

nav a {
    padding: 14px 16px;
    text-decoration: none;
}

ul {
    display: flex;
    justify-content: right;
}

.buttonNav {
    background-color: black;
    font-family: 'Montserrat', sans-serif;
    display: inline;
    text-align: right;
    font-size: 30px;
    margin-right: 30px;
    font-weight: 1000;
    margin-bottom: 10px;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0.15rem;
}

ul {
    padding-top: 5px;
}

footer {
    position:fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgb(58, 57, 57);
    text-align: center;
    color: greenyellow;
    height: 5px;
    padding: 30px;
    margin-top: 30%;
}

footer p {
    margin-bottom: 20px;
    margin-top: 10px;
    font-size: 12px;
}

.icon{
    color: white;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: -15px;
    font-size: 1rem;
}

nav .buttonNav {
    animation: background-pan 8s linear infinite;
    background: linear-gradient(to right, var(--pink), var(--lightpurple), var(--lightgreen), var(--green), var(--darkgreen), var(--blue), var(--plum), var(--blue), var(--darkgreen), var(--green), var(--lightgreen), var(--lightpurple), var(--pink));
    background-size: 200%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    white-space: nowrap;
}

